<template>
  <div class="content-body">
    <div class="container">
      <div class="col-xxl-12">
        <p>SVC Total max. supply: <strong>210 million</strong></p>
        <div>
          <h2>Reaperville Staking Statistics</h2>
          <h4 class="mt-4">Earnable reward tokens:</h4>
          <ul>
            <li>
              <strong>5 million in total for Reaperville</strong>
            </li>
            <li>
              <p>1 million in first year</p>
            </li>
            <li>
              <p>2.5 million in first 3 years</p>
            </li>
          </ul>
          <h4 class="mt-4">
            <p>
              Total staked power: <strong>{{ totalStakedPower }}</strong>
            </p>
            <p>
              Base staking power:
              <strong
                >{{ baseStakingPower }} ==
                {{
                  Math.round((baseStakingPower / totalStakedPower) * 10000) /
                  100
                }}%</strong
              >
            </p>
          </h4>
          <p>Stake and upgrade add 1% to base staking power</p>
          <p>Unstake adds 5% to base staking power</p>
          <p>Emergency unstake adds 0% to base staking power</p>
          <h4>
            <p>
              Reward tokens minted:
              <strong
                >{{
                  Math.round(
                    (5 * 10 ** 24 - Number(rewardTokenMintAllowance4Reaper)) /
                      10 ** 18
                  )
                }}
                SVC</strong
              >
            </p>
            <p>
              Reaperville total emission rate:
              <strong
                >{{ rewardTokenEmissionPerHour }} SVC / hour @100%
                payout</strong
              >
            </p>
            <p>
              =
              {{
                Math.round((rewardTokenEmissionPerHour * 24 * 365) / 1000) /
                1000
              }}
              million SVC / year
            </p>
          </h4>

          <hr class="mt-4 mb-4" />

          <h1 class="mb-0 mt-4">Everything below is ADMIN ONLY</h1>
          <h2 v-if="!isAdmin" class="mt-0 mb-4">
            (You are not logged into the admin wallet)
          </h2>
          <h2 v-else class="mt-0 mb-4">(YOU ARE THE ADMIN)</h2>

          <hr class="mt-4 mb-4" />

          <h2>Reward Token SVC</h2>
          <input
            id="tokensToMint"
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowance4NewMinter"
          />
          <input
            id="addressTo"
            type="text"
            class="form-control mx-auto"
            v-model="addressTo"
          />

          <button
            class="btn btn-danger"
            :disabled="!isAdmin"
            @click="
              registerAddressAsMinter(
                addressTo,
                rewardTokenMintAllowance4NewMinter
              )
            "
          >
            Register {{ addressTo }} as minter with allowance
            {{
              Math.round(rewardTokenMintAllowance4NewMinter / 10 ** 15) /
              10 ** 3
            }}
            SVC
          </button>

          <!-- &nbsp;

          <button
            class="btn btn-primary"
            :disabled="!isAdmin"
            @click="mintRewardToken(addressTo, rewardTokenMintAllowance4NewMinter)"
          >
            Mint {{ rewardTokenMintAllowance4NewMinter }} SVC
          </button> -->

          <h6>{{ feedbackMessage }}</h6>

          <hr />

          <h2 class="mb-0">Card Contract</h2>
          <div>
            Card contract can still mint
            <strong>
              {{ Math.round(rewardTokenMintAllowance4Cards / 10 ** 18) }}
            </strong>
            SVC =
            <strong>
              {{
                Math.round(rewardTokenMintAllowance4Cards / 10 ** 21) / 10 ** 3
              }}
            </strong>
            M SVC
          </div>
          <input
            id="rewardTokenMintAllowance4Cards"
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowance4Cards"
          />
          <div>
            <button
              class="btn btn-success"
              :disabled="!isAdmin"
              type="button"
              @click="
                setNewMintAllowance('cards', rewardTokenMintAllowance4Cards)
              "
            >
              Update Card's mint allowance
            </button>
          </div>

          <br />

          <h2 class="mb-0">Reaperville Contract</h2>
          <div>
            Reaperville contract can still mint
            <strong>
              {{ Math.round(rewardTokenMintAllowance4Reaper / 10 ** 18) }}
            </strong>
            SVC =
            <strong>
              {{
                Math.round(rewardTokenMintAllowance4Reaper / 10 ** 21) / 10 ** 3
              }}
            </strong>
            M SVC
          </div>
          <input
            id="rewardTokenMintAllowance4Reaper"
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowance4Reaper"
          />
          <div>
            <button
              class="btn btn-success"
              :disabled="!isAdmin"
              type="button"
              @click="
                setNewMintAllowance('reaper', rewardTokenMintAllowance4Reaper)
              "
            >
              Update Reaper's mint allowance
            </button>
          </div>

          <br />

          <h4>Reaper Contract Balances</h4>
          <p>
            {{ Math.round(reaperBchBalance / 10 ** 15) / 10 ** 3 }}
            BCH
          </p>
          <div>
            <button
              class="btn btn-primary"
              type="button"
              :disabled="reaperBchBalance <= 0 || !isAdmin"
              @click="withdrawBchFromReaper"
            >
              withdraw BCH
            </button>
          </div>

          <br />

          <h4>Reaper Mint Price</h4>
          <p>
            {{ Math.round(reaperBchPerNft / 10 ** 12) / 10 ** 6 }}
            BCH
          </p>
          <input
            id="reaperBchPerNft"
            type="text"
            class="form-control mx-auto"
            v-model="reaperBchPerNft"
          />
          <div>
            <button
              class="btn btn-primary"
              :disabled="!isAdmin"
              type="button"
              @click="setReaperBchPerNft"
            >
              set new price
              {{ Math.round(reaperBchPerNft / 10 ** 12) / 10 ** 6 }} BCH
            </button>
          </div>

          <br />

          <h2 class="mb-0">Pause Reaper Mint</h2>
          Mint is active: {{ !reaperMintPaused }}
          <p>
            <button
              @click="setReaperMintPaused(true)"
              :disabled="reaperMintPaused || !isAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setReaperMintPaused(false)"
              :disabled="!reaperMintPaused || !isAdmin"
              class="btn btn-success"
            >
              Unpause
            </button>
          </p>

          <h2 class="mb-0">Pause Reaper Staking</h2>
          Staking is active: {{ !reaperStakingPaused }}
          <p>
            <button
              @click="setReaperStakingPaused(true)"
              :disabled="reaperStakingPaused || !isAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setReaperStakingPaused(false)"
              :disabled="!reaperStakingPaused || !isAdmin"
              class="btn btn-success"
            >
              Unpause
            </button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import Cards from "../../artifacts/contracts/Cards.sol/Cards.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      rewardTokenContract: null,
      reaperContract: null,
      cardsContract: null,

      reaperBchBalance: 0,
      reaperBchPerNft: "0",

      rewardTokenMintAllowance4NewMinter: "10000000000000000000000",
      addressTo: "",

      feedbackMessage: "",

      rewardTokenMintAllowance4Reaper: "0",
      rewardTokenMintAllowance4Cards: "0",

      isAdmin: false,

      reaperMintPaused: false,
      reaperStakingPaused: false,

      allowanceToSet: 5000000,
      totalStakedPower: 0,
      baseStakingPower: 0,
      released: 0,
      rewardTokenEmissionPerHour: 0,
    };
  },
  methods: {
    mintRewardToken: async function (addressTo, amount) {
      let tx = await this.rewardTokenContract.mint(
        addressTo,
        ethers.utils.parseUnits(amount.toString(), "ether")
      );
      await tx.wait();
    },

    registerAddressAsMinter: async function (addressTo, newAllowance) {
      this.feedbackMessage = "";
      let minterAllowance =
        await this.rewardTokenContract.remainingMintersAllowance(addressTo);
      if (minterAllowance > 0) {
        this.feedbackMessage = addressTo + " is already registered as minter";
        return false;
      }

      let tx = await this.rewardTokenContract.updateMintersAllowance(
        addressTo,
        newAllowance
      );
      await tx.wait();
      window.location.reload();
    },

    setNewMintAllowance: async function (_contractName, _newAllowance) {
      let contractAddress;
      switch (_contractName) {
        case "reaper":
          contractAddress = window.ReaperAddress;
          break;
        case "cards":
          contractAddress = window.CardsAddress;
          break;
        default:
          this.feedbackMessage = "Contract not implemented";
          return false;
      }
      if (_newAllowance === 0) {
        let tx = await this.rewardTokenContract.removeMinter(contractAddress);
        await tx.wait();
        window.location.reload();
      } else {
        let tx = await this.rewardTokenContract.updateMintersAllowance(
          contractAddress,
          _newAllowance
        );
        await tx.wait();
        window.location.reload();
      }
    },

    setReaperMintPaused: async function (newPaused) {
      let tx = await this.reaperContract.setMintPaused(newPaused);
      await tx.wait();
      window.location.reload();
    },
    setReaperStakingPaused: async function (newPaused) {
      let tx = await this.reaperContract.setStakingPaused(newPaused);
      await tx.wait();
      window.location.reload();
    },

    setReaperBchPerNft: async function () {
      let tx = await this.reaperContract.setBchPerNft(
        String(this.reaperBchPerNft)
      );
      await tx.wait();
      window.location.reload();
    },

    withdrawBchFromReaper: async function () {
      let tx = await this.reaperContract.withdraw();
      await tx.wait();
      window.location.reload();
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        this.rewardTokenContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.signer
        );
        this.reaperContract = new ethers.Contract(
          window.ReaperAddress,
          Reaperville.abi,
          this.signer
        );
        this.cardsContract = new ethers.Contract(
          window.CardsAddress,
          Cards.abi,
          this.signer
        );
        // this.flexUsdTokenContract = new ethers.Contract(
        //   window.FlexUsdAddress,
        //   RewardToken.abi,
        //   this.signer
        // );
      })
      .then(() => {
        this.signer.getAddress().then((address) => {
          this.addressTo = address;
          if (address === window.DevAddress) {
            this.isAdmin = true;
          }
        });
        this.reaperContract
          .showStakerInfo(window.DevAddress)
          .then((stakerInfo) => {
            this.totalStakedPower = stakerInfo[2];
            this.baseStakingPower = stakerInfo[3];
            this.rewardTokenEmissionPerHour = String(
              Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
                100000
            );
          });
        this.provider.getBalance(window.ReaperAddress).then((balance) => {
          this.reaperBchBalance = String(balance);
        });
        this.reaperContract.isMintPausedStakingPaused().then((result) => {
          this.reaperMintPaused = result[0];
          this.reaperStakingPaused = result[1];
        });
        this.reaperContract.bchPerNft().then((_bchPerNft) => {
          this.reaperBchPerNft = String(_bchPerNft);
        });

        this.rewardTokenContract
          .remainingMintersAllowance(window.ReaperAddress)
          .then((_allowance) => {
            this.rewardTokenMintAllowance4Reaper = String(_allowance);
          });
        this.rewardTokenContract
          .remainingMintersAllowance(window.CardsAddress)
          .then((_allowance) => {
            this.rewardTokenMintAllowance4Cards = String(_allowance);
          });
      });
  },
};
</script>
